@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
li {
  margin-top: 10rem;
  display: flex;
  flex-direction: column;
}
a {
  color: #fff;
  text-decoration: none;
  margin-top: 1rem;
  transition: 0.3s;
}

a:hover {
  color: blueviolet;
}

ul {
  height: 100%;

  justify-content: left;
  font-size: 3vw;
  padding: 4rem;
  list-style: none;

  overflow: hidden;
  text-align: left;
}

* {
  padding: 0px;
  margin: 0px;
  outline: 0;
  box-sizing: border-box;
}

body,
html {
  height: 100%;
  font: 480 14px roboto, sans-serif;
  background-image: url(/portfolio/static/media/img6.c774d900.jpg);
  background-position: center;
}

